.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.loader div{
  animation-duration: 0.5s;
}

.loader div:first-child {
  animation-delay: 0.1s;
}

.loader div:nth-child(2) {
  animation-delay: 0.3s;
}

.loader div:nth-child(3) {
  animation-delay: 0.6s;
}

.login-form input{
  width: 100%;
  margin-top: 13px;
  padding: 5px;
  font-size: 17px;
  border: 1px solid gray;
  border-radius: 5px;
}

#submit-btn{
  background-color: rgb(58, 43, 43);
  border: 3px solid rgb(58, 43, 43);
  border-radius: 4px;
  color: white;
  cursor: pointer;
}

.social-btn button{
  padding: 5px;
  width: 60%;
  margin-top: 10px;
  background-color: white;
  border: 1px solid rgb(141, 133, 133);
  border-radius: 10px;
  font-size: 18px;
}

.social-btn button:hover{
  color: blue;
  background-color: lightgray;
  font-size: 18px;
}

.create-btn{
  border: none;
  background-color: white;
  color: blue;
  font-weight: bold;
}

.create-btn:hover{
  text-decoration: underline;
}
